/*******************************************************************************
 * Themes
 ******************************************************************************/
@media (prefers-color-scheme: light) { 
 html/*:root*/ {
 --bg-color: #{$white-color};
 --code-bg-color: #f2f2f2;//#{$grey-lighter-color};
 --text-color: #505050;//#4e4e4e;
 --theme-color: #{$cyan-darker-color};//#337ab7;//#2682BA;
 --navbar-bg-color: #282828;//#{$grey-dark-color};//dark bg
 --divider-color: rgba(0,0,0,.1);

 --profile-pic-opacity: 1;
 --logo-filter: inherit;

 .fa-sun {
 display : none;
 }
 .fa-moon {
 padding-left: 10px;
 padding-top: 12px;
 display : block;
 }
 }
}

@media (prefers-color-scheme: dark) {
 html/*[data-theme='dark']*/ {
 --bg-color: #{$grey-dark-color};
 --code-bg-color: #2c3237;
 --text-color: #{$grey-light-color};
 --theme-color: #{$cyan-light-color};
 --navbar-bg-color: var(--bg-color);
 --divider-color: #424246;

 --profile-pic-opacity: .8;
 --logo-filter: invert(1);

 .fa-sun {
 padding-left: 10px;
 padding-top: 12px;
 display : block;
 }
 .fa-moon {
 display : none;
 }
 }
}

