@charset "utf-8";

// Dimensions
$max-content-width: 780px;

@import "variables", "themes", "layout", "base";

/* ================= my custom styling================== */

html, body { 
 font-size: 16px;
 line-height: 1.5;
 //font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
 //-webkit-font-smoothing: antialiased;
 //-moz-osx-font-smoothing: grayscale;

 //push the footer to the bottom
 height: 100%;
 display: flex;
 flex-direction: column;
}

.main-container {
 padding-left: .75em;
 padding-right: .75em;

 flex: 1;
 display: flex;
}

table {
 th, td {
 font-size: 1em;
 padding-left: 0;
 padding-right: 1em;
 padding-top: .5em;
 padding-bottom: .5em;
 vertical-align: top;
 }
}

.item-date {
 white-space: nowrap;
}

h1, h2 {
 opacity: .98;
}

h1 {
 font-size: 2.25em;
}

.justified {
 text-align: justify;
 text-justify: inter-word;
}

.about-header {
 padding: 0;
 margin-top: auto;
 margin-bottom: auto;
 div {
 margin: 0;
 padding: 0;
 }
}

.about-div {
 margin-left: 0;
 padding-bottom: 1.25em;
}

.contact-icons {
 text-align: center;
 margin-top: 3em;
 a>i {
 margin: .2em;
 font-size: 2.5em;
 color: var(--text-color);
 &:hover {
 color: var(--theme-color);
 }
 }
}

span.author::after, span.title::after {
 content: "\a";
 white-space: pre;
}

.profile-pic {
 margin: 0;
 padding: 0;
 margin-top: auto;
 margin-bottom: auto;
 
 max-width: 125px;
 img {
 width: 100%;
 //opacity: var(--profile-pic-opacity);
 border-radius: 50%;
 padding: 2px;
 border: 1px solid var(--divider-color);
 }
}

.profile {
 //max-width: 175px;
 //padding-left: 10px;
 //padding-bottom: 10px;
 //padding-right: 10px;

 img {
 opacity: var(--profile-pic-opacity);
 border-radius: 12px;
 padding: 5px;
 border: 1px solid var(--divider-color);
 }
}

.post-description a {
 font-weight: inherit;
 color: var(--theme-color);

 &:hover {
 text-decoration: underline;
 }
}

footer {
 flex: 0;
 text-align: center;
 font-size: .7rem;
 line-height: 1.4em;
 padding-top: 1.2em;
 padding-bottom: 1.2em;
 border-top: 1px solid var(--divider-color);

 a {
 color: var(--text-color);
 text-decoration: underline;
 }
}

.navbar {
 opacity: 1;

 *:focus {
 outline: 0;
 }

 .navbar-brand {
 font-size: 1.1em;
 }

 .navbar-brand.logo>a>img {
 opacity: .75;
 height: 1.5em;
 -webkit-filter: var(--logo-filter);
 filter: var(--logo-filter);
 }
}

/* begin dark navbar styling */
.navbar {
 background-color: var(--navbar-bg-color);

 .navbar-brand {
 div {
 color: $grey-light-color;
 &:hover {
 color: white;
 }
 }
 }
 .navbar-nav .nav-item .nav-link {
 background-color: var(--navbar-bg-color);
 color: $grey-light-color;
 //font-weight: normal;
 &:hover {
 color: white;
 text-decoration: none;
 }
 }
 .navbar-nav .nav-item.active>.nav-link {
 font-weight: normal;
 color: white;
 &:hover {
 color: white;
 }
 }
}

.navbar .dropdown-menu {
 background-color: var(--navbar-bg-color);
 border: 1px solid #424246;//var(--divider-color);
 a:not(.active) {
 color: $grey-light-color;
 }
 a:hover {
 color: white;
 }
 .dropdown-divider {
 border-top: 1px solid #424246 !important;//var(--divider-color) !important;
 }
}

.dropdown-item {
 color: $grey-light-color;
 &:hover {
 color: white;
 background-color: var(--navbar-bg-color);
 }
}

.navbar-toggler {
 .icon-bar {
 background-color: $grey-light-color;//var(--text-color);
 }
}
/* end dark navbar styling */

.publications {
 h2.year {
 padding-top: 0;
 margin-top: 0;
 margin-bottom: .35em;
 border-top: none;
 padding-bottom: .25em;
 text-align: center;
 color: var(--divider-color);
 border-bottom: 1px solid var(--divider-color);
 }

 ol.bibliography {
 li {
 .author {
 >.author-self {
 border-bottom: 1px dashed;
 }
 }
 .links {
 a {
 //text-transform: lowercase;
 }
 }
 }
 }
}

pre {
 border: 1px solid var(--divider-color);
}

.publications {
 ol.bibliography {
 list-style: inherit;
 padding-left: 1em;
 }
}

@media (max-width: 768px) {
 .profile {
 max-width: 100%;
 }
}

/* responsive design */
@media (max-width: 576px) {
 html, body {
 //font-size: inherit;
 }

 .main-container {
 padding-left: inherit;
 padding-right: inherit;
 }

 .news-n-award {
 font-size: 1em;
 }

 .profile {
 img {
 display: block;
 margin: auto;
 border-radius: 50%;
 width: 170px;
 height: 170px;
 object-fit: cover;
 //object-position: 0px -15px;
 }

 padding-left: inherit;
 padding-right: inherit;
 padding-bottom: 30px;
 }

 .navbar .dropdown-menu {
 a {
 text-align: right;
 }
 }

 span.author::after, span.title::after {
 content: ".";
 white-space: inherit;
 }
}